.astroData {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: space-around;
  flex-wrap: wrap;
}

.invisi {
  display: none;
}

@media screen and (max-width: 664px) {
  .astroData {
    display: grid;
    grid-template-columns: repeat(2, 150px);
    justify-content: space-around;
    overflow-y: scroll;
    overflow-x: hidden;
  }
}/*# sourceMappingURL=Astro.css.map */