@import '../../../../source.scss';

.logoContainer {
    height: 100%;
    width: 40%;
    @include disp(row, flex-start, space-around);
}

.logo {
    width: 48px;
    height: 48px;
    margin-top: auto;
    margin-bottom: auto;
    margin-right: 12px;
}

.logoTitle {
    font-size: 36px;
    color: white;
}

@media (min-width: 768px) and (max-width:1024px) {
    .logoContainer{
        width: 30%;
    }

    .logo {
        width: 2rem;
        height: 2rem;
    }

    .logoTitle {
        font-size: 1.9rem;
    }
}

@media (min-width: 512px)and (max-width: 767px) {
    .logoContainer{
        width: 50%;
    }

    .logo {
        width: 1.5rem;
        height: 1.5rem;
    }

    .logoTitle {
        font-size: 1.4rem;
    }
}

@media (min-width: 321px) and (max-width: 511px) {
    .logoContainer{
        width: 50%;
    }

    .logo {
        width: 1.6rem;
        height: 1.6rem;
        margin-right: 4px;
    }

    .logoTitle {
        font-size: 1.3rem;
    }
}

@media screen and (max-width: 320px) {
    .logoContainer{
        width: 45%;
    }

    .logo {
        width: 1.2rem;
        height: 1.2rem;
        margin-right: 4px;
    }

    .logoTitle {
        font-size: 1rem;
    }
}