@import "../../../../source";

.astroData{
    width: 100%;
    height: 100%;
    @include disp();
}

.invisi{
    display: none;
}

@media screen and (max-width: 664px){
    .astroData{
        display: grid;
        grid-template-columns: repeat(2, 150px);
        justify-content: space-around;
        overflow-y: scroll;
        overflow-x: hidden;
    }
}