@import '../../../source';

.GFooter{
    width: 100%;
    height: 14%;
    border-top: 2px solid rgba(0, 0, 0, 10%);
    @include disp();
}

.GFButton{
    background-color: rgba(255, 255, 255, 30%);
    font-size: 24px;
    border: none;
    box-shadow: 4px 4px 28px 0px rgba(0, 0, 0, 50%);
    border-radius: 2rem;
    font-family: 'Urbanist', sans-serif;
    padding-left: 10px;
    padding-right: 10px;
}

.GFButton:active {
    box-shadow: 0px 8px 56px 0px rgba(0, 0, 0, 50%);
}

@media (min-width: 304px) and (max-width: 530px){
    .GFButton{
        font-size: 16px;
    }
}

@media  screen and (max-width: 305px) {
    .GFButton{
        font-size: 12px;
    }
}