.MSearchBox {
  background-color: rgba(255, 255, 255, 0.15);
  border: none;
  box-shadow: 0px 8px 24px 0px rgba(0, 0, 0, 0.3);
  border-radius: 2rem;
  font-family: "Urbanist", sans-serif;
  height: 36px;
  width: 400px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: space-around;
  flex-wrap: wrap;
  margin-top: auto;
  margin-bottom: auto;
  margin-right: 20px;
}

@media (min-width: 950px) and (max-width: 1500px) {
  .MSearchBox {
    height: 36px;
    width: 300px;
  }
}
@media screen and (max-width: 949px) {
  .MSearchBox {
    display: none;
  }
}/*# sourceMappingURL=MSearch.css.map */