.lSearch {
  text-align: center;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  width: 80%;
  font-size: 16px;
  color: inherit;
}

.lSearch:focus {
  border: none;
  outline: none;
}

@media (min-width: 512px) and (max-width: 1024px) {
  .lSearch {
    width: 85%;
    font-size: 12px;
  }
}
@media screen and (max-width: 511px) {
  .lSearch {
    width: 75%;
    font-size: 0.3;
  }
}/*# sourceMappingURL=search.css.map */